//Carousel.js
import { StyledCarousel, } from './Carousel.styled';
import React , { useState, useEffect } from 'react';
import image1 from '../../assets/images/dogCarousel3.jpg';
import image2 from '../../assets/images/dogCarousel4.jpg';
import image3 from '../../assets/images/dogCarouselCat.jpg';

const Carousel = () => {
  let slides = [image3,image1,image2,image3,image1];

  const [current, setCurrent] = useState(0);

  useEffect(() => {
    const next = (current+1) % slides.length;
    const id  = setTimeout(() => setCurrent(next), 3000);
    return () => clearTimeout(id);
  }, [current]);

  return (
    <StyledCarousel>
        <img src={slides[current]}/>
        <div>
          <p> LOVE PETS? SO DO WE!</p>
        </div>
    </StyledCarousel >
)};

export default Carousel;