// Carousel.styled.js
import styled , {keyframes } from 'styled-components';

const kf = keyframes`
  from{
    opacity:0;
  }
  to{
    opacity:1;
  }`;

export const StyledCarousel = styled.div`
  width:100vw;
  aspect-ratio:3/2;
  // position:relative;
  position:absolute;
  top:0;
  left:0;
  overflow:hidden;

  img{
    position:absolute;
    // top:0;
    width:100%;
    height:100%;
    // transition: opacity 3s ease-in-out;
    -webkit-transition: opacity 3s ease-in-out;
    z-index:-1;
    left:0;
    // opacity:1;
    animation: ${kf} 3s;
  }

  div{
    width:20vw;
    color:white;
    position:absolute;
    left:8%;
    top:35%;
    display:flex;
    flex-direction: column;
    align-items: center;
    font-size:2em;
    text-align:center;
    font-weight:bold;
    background-color: rgba(25,25,25,0.5);
    border-radius: 30px 5px;
    padding:20px;
    border:3px solid rgba(5,5,5,0.5);

    @media (max-width:768px){
      display:none;
    }
  }

  p{
    padding:0;
  }
`;